import { _decorator, Component, Node, find } from 'cc';
import { BaseLayer } from '../../framework/common/BaseLayer';
import { audioManager } from '../../framework/manager/AudioManager';
import { constants } from '../data/Constants';
import { msgcmd } from '../data/MsgCmd';
import { gameModel } from '../model/GameModel';
import { localText } from '../data/LocalText';
import { layerManager } from '../../framework/manager/LayerManager';

const { ccclass, property } = _decorator;

@ccclass('LossLayer')
export class LossLayer extends BaseLayer {

    onLoad() {
        super.onLoad();
    }

    onEnable() {
        super.onEnable();

        this.addEventArr([
            msgcmd.mapLeave,
        ]);
    }

    initUI() {
    }

    onShowRecipeBtnClick() {
        audioManager.playEffect(constants.audioNames.btnClick);
        super.closeLayer();
    }

    onBackBtnClick() {
        audioManager.playEffect(constants.audioNames.btnClick);
        // 通知GameLayer返回HomeLayer
        gameModel.mapLeave({});
    }

    onRestartBtnClick() {
        audioManager.playEffect(constants.audioNames.btnClick);
        layerManager.openLayer(constants.layers.GameLayer);
        super.closeLayer();
    }

    mapLeaveEventCb() {
        this.closeLayer();
    }
}
